<template>
  <div class="container">
    <header>
      <h3>Notlar</h3>
    </header>

    <div class="table-wrapper">
      <table class="table-users" id="my-table">
        <tr>
          <th style="width: 50%">Notlar</th>
          <th style="width: 30%">Not Saati</th>
          <th style="width: 20%">lem</th>
        </tr>
        <tr v-for="not in NotlarDizi" :key="not.key">
          <td>{{ not.not_icerik }}</td>
          <td>{{ not.not_saati }}</td>
          <td>
            <router-link
              :to="{ name: 'notedit', params: { id: not.key } }"
              class="btn btn-edit"
            >
              <i class="material-icons">create</i>
            </router-link>
            <button @click.prevent="deleteNot(not.key)" class="btn btn-delete">
              <i class="material-icons">remove_circle_outline</i>
            </button>
          </td>
        </tr>
      </table>

      <router-link to="/notadd"
        ><button class="btn btn-add">Not Ekle</button></router-link
      >

      <button class="btn btn-export" @click="exportPDF()">Rapor Al</button>

      <button @click="deleteAll()">deleteAll</button>
    </div>
  </div>
</template>



   
<script>
import { db } from "../firebaseDb";

import jsPDF from "jspdf";
import autoTable from "jspdf-autotable";

import trLocale from "moment/locale/tr";
import moment from "moment";

/*
import RobotoBold from '../assets/fonts/Roboto-Bold-normal.js';

*/

export default {
  data() {
    return {
      NotlarDizi: [],
      sayac: 0,
    };
  },
  created() {
    db.collection("notlar").onSnapshot((snapshotChange) => {
      this.NotlarDizi = [];
      snapshotChange.forEach((doc) => {
        this.NotlarDizi.push({
          key: doc.id,
          not_icerik: doc.data().not_icerik,
          not_saati: doc.data().not_saati,
        });
      });
    });
  },

  methods: {
    deleteNot(id) {
      if (window.confirm("Silmek stiyor musunuz?")) {
        db.collection("notlar")
          .doc(id)
          .delete()
          .then(() => {
            console.log("Document deleted!");
          })
          .catch((error) => {
            console.error(error);
          });
      }
    },
    exportPDF() {
      moment.updateLocale("tr", trLocale);

      var currentDate = moment().format("LL");
      var currentDay = moment().format("dddd");
      var currentClock = moment().format("LTS");

      var fullTime =
        currentDate + " " + currentDay + " " + "Saat: " + currentClock;

      var doc = new jsPDF();

  doc.addFont("Arimo-Regular.ttf", "Arimo", "normal");
  doc.addFont("Arimo-Bold.ttf", "Arimo", "bold");
  doc.setFont("Arimo");
  doc.setFontType("normal");
  doc.setFontSize(28);


    /*** 
doc.addFont('Roboto-Bold.ttf', 'Roboto-Bold', 'normal');
doc.setFont('Roboto-Bold');



console.log(doc.getFontList());
     doc.setFont("Times");
     doc.setFontType("normal"); 
     doc.setFont("Times");
   doc.setFont('Roboto');
   
   */

   /*** */



      autoTable(doc, { html: "#my-table" });

      doc.save(fullTime + ".pdf");
    },

    /*Delete */
    deleteAll() {
      for (var i = 0; i < this.NotlarDizi.length; i = 1) {
        this.NotlarDizi = [];
      }
    },
    /*Delete */
  },
};
</script>


